import React, { useState } from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import MenuRoundedIcon from "@material-ui/icons/MenuRounded";
import MenuOpenRoundedIcon from "@material-ui/icons/MenuOpenRounded";
import Avatar from "@material-ui/core/Avatar";
import Logo from "../../marker/Logo.png";
import SideMenu from "./SideMenu";

const Navbar = () => {
  const [showSideMenu, setshowSideMenu] = useState(false);
  const showCheckpointList = () => {
    setshowSideMenu(!showSideMenu);
  };

  return (
    <div className="menu-navbar">
      <AppBar position="static">
        <Toolbar variant="dense">
          <Typography variant="h6" color="inherit">
            <Avatar alt="Remy Sharp" src={Logo} />
          </Typography>

          <Typography variant="h6" color="inherit" className="ml-3">
            <div>About</div>
          </Typography>

          <div style={{ width: "100%" }}>
            <IconButton
              edge="start"
              color="inherit"
              aria-label="menu"
              style={{ float: "right" }}
              onClick={showCheckpointList}
            >
              {showSideMenu ? <MenuOpenRoundedIcon /> : <MenuRoundedIcon />}
            </IconButton>
          </div>
        </Toolbar>
      </AppBar>

      <div
        className="menu-sidebar"
        style={{
          transform: showSideMenu
            ? "translate(0px, 0px)"
            : "translate(500px, 0px)",
          display: showSideMenu ? true : "none",
        }}
      >
        <SideMenu />
      </div>
    </div>
  );
};

export default Navbar;
